export default {
  name: 'markdown',
  data () {
    return {
      content: ''
    }
  },
  methods: {
    getMsg () {
      const query = {
        url: './data/markdownData.md',
        method: 'get',
        responseType: 'text'
      }
      this.$fetchData(query).then(res => {
        this.content = res.data
      }).catch(error => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.getMsg()
  }
}
